import React from 'react';
import Modal from 'react-modal';

const customStyles = {
    content : {
        top                   : '50%',
        left                  : '50%',
        right                 : 'auto',
        bottom                : 'auto',
        marginRight           : '-50%',
        transform             : 'translate(-50%, -50%)',
        padding:0
    }
};


class ModalProject extends React.Component {

    render() {
        console.log(this.props.project,"ppppp")
        if(Object.keys(this.props.project).length!==0)
        return (
            <div>
                <Modal
                    isOpen={this.props.modalIsOpen}
                    onAfterOpen={this.props.afterOpenModal}
                    onRequestClose={this.props.closeModal}
                    style={customStyles}
                    contentLabel="Example Modal"
                >
                    <div className="modal-content">
                        <div className="modal-header" style={{backgroundImage: 'url(images/portfolio/'+this.props.project.image+')'}}>
                        </div>
                        <div className="modal-body">
                            <h2>{this.props.project.title}</h2>

                            <p>Some text in the Modal Body</p>
                            <p>Some other text...</p>
                        </div>
                        <div className="modal-footer">
                            {/*<h3>Modal Footer</h3>*/}
                        </div>
                    </div>
                </Modal>
            </div>
        );
        else return ""
    }
}

export default ModalProject;



